import React from 'react';
import { DataGrid, GridToolbarContainer, GridToolbarExport} from '@mui/x-data-grid';

const columns = [
    { field: 'no', headerName: 'No', sortable: false, width: 80 },
    { field: 'dt', headerName: '', sortable: false, width: 100 },
    // debtor
    { field: 'dr_sbj1', headerName: '', sortable: false, width: 100 },
    { field: 'dr_sbj2', headerName: '', sortable: false, width: 100 },
    { field: 'dr_taxg', headerName: '', sortable: false, width: 100 },
    { field: 'dr_dept', headerName: '', sortable: false, width: 100 },
    { field: 'dr_amount', headerName: '()', sortable: false, width: 100 },
    { field: 'dr_tax', headerName: '', sortable: false, width: 100 },
    // creditor
    { field: 'cr_sbj1', headerName: '', sortable: false, width: 100 },
    { field: 'cr_sbj2', headerName: '', sortable: false, width: 100 },
    { field: 'cr_taxg', headerName: '', sortable: false, width: 100 },
    { field: 'cr_dept', headerName: '', sortable: false, width: 100 },
    { field: 'cr_amount', headerName: '()', sortable: false, width: 100 },
    { field: 'cr_tax', headerName: '', sortable: false, width: 100 },
    // other
    { field: 'remarks', headerName: '', sortable: false, width: 100 },
    { field: 'note', headerName: '', sortable: false, width: 100 },
    { field: 'tag', headerName: '', sortable: false, width: 100 },
    { field: 'type', headerName: 'MF', sortable: false, width: 100 },
    { field: 'adjust', headerName: '', sortable: false, width: 100 },
    { field: 'dt_create', headerName: '', sortable: false, width: 100 },
    { field: 'dt_update', headerName: '', sortable: false, width: 100 },
];

function CustomToolbar(csvOptions: any) {
    return (
        <GridToolbarContainer>
            <GridToolbarExport
                printOptions={{ disableToolbarButton: true }}
                csvOptions={csvOptions}
            />
        </GridToolbarContainer>
    );
}

function JournalBook(props: any) {
    return (
        <DataGrid
            rows={props.rows}
            columns={columns}
            disableSelectionOnClick
            components={{
                Toolbar: CustomToolbar,
            }}
            componentsProps={{
                toolbar: {fileName: props.fileName}
            }}
        />
    );
}

export default JournalBook;
